@charset "utf-8";

@font-face {
	font-family: "SukhumvitSet";
    src: url('font/sukhumvitset-webfont.eot');
    src: url('font/sukhumvitset-webfont.eot?#iefix') format('embedded-opentype'),
         url('font/sukhumvitset-webfont.woff2') format('woff2'),
         url('font/sukhumvitset-webfont.woff') format('woff'),
         url('font/sukhumvitset-webfont.ttf') format('truetype'),
         url('font/sukhumvitset-webfont.svg#SukhumvitSet') format('svg');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'open_sansregular';
    src: url('font/opensans-regular-webfont.eot');
    src: url('font/opensans-regular-webfont.eot?#iefix') format('embedded-opentype'),
         url('font/opensans-regular-webfont.woff2') format('woff2'),
         url('font/opensans-regular-webfont.woff') format('woff'),
         url('font/opensans-regular-webfont.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}
